import React, { Component } from "react";
import { Button } from "react-bootstrap";
import { useNavigate } from "react-router-dom";

export const Tombol = ({ conten }) => {
  const Navigate = useNavigate();
  return (
    <div>
      <Button
        onClick={function () {
          Navigate("/Info");
        }}
      >
        {conten}
      </Button>
    </div>
  );
};
