import React, { Component } from "react";
import { Navbar, Container, Nav, Button } from "react-bootstrap";
import { BrowserRouter as Router, Switch, Route, Link, Routes } from "react-router-dom";
import Admin from "../Admin";
import JoinUs from "../JoinUs";
import Pembahasan from "../Pembahasan";
import Tryout from "../Tryout";
import Home from "../Home";
import { Tombol } from "../Button/Tombol";
import Info from "../InfoCpns/Info";
import "./Navigation.css";
import panel1 from "../panelNavigation/panel1";
import PanelTips from "../panelNavigation/panelTips";
import { Panel2 } from "../panelNavigation/panel2";
import PanelSoal from "../panelNavigation/panelSoal";
import AturanGroup from "../panelNavigation/aturanGroup";

export default class Navigation extends Component {
  render() {
    return (
      <div>
        <Router>
          <div>
            <Navbar bg="primary" expand="lg">
              <Container>
                <Navbar.Brand className="judul">pejuang sukses</Navbar.Brand>
                <Navbar.Toggle aria-controls="navbarScroll" />
                <Navbar.Collapse id="navbarScroll">
                  <Nav className=" title ms-auto my-2 my-lg-0" style={{ maxHeight: "100px" }} navbarScroll>
                    <Nav.Link as={Link} to={"/home"}>
                      HOME
                    </Nav.Link>
                    <Nav.Link as={Link} to={"/admin"}>
                      ADMIN
                    </Nav.Link>
                    <Nav.Link as={Link} to={"/tryout"}>
                      TRYOUT
                    </Nav.Link>
                    <Nav.Link as={Link} to={"/pembahasan"}>
                      PEMBAHASAN
                    </Nav.Link>
                    <Nav.Link as={Link} to={"/joinUs"}>
                      JOIN US
                    </Nav.Link>
                  </Nav>
                </Navbar.Collapse>
              </Container>
            </Navbar>
          </div>

          <div>
            <Routes>
              <Route exact path="/" element={<Home />} />
              <Route exact path="/Home" element={<Home />} />
              <Route path="/ADMIN" element={<Admin />} />
              <Route path="/Tryout" element={<Tryout />} />
              <Route path="/Pembahasan" element={<Pembahasan />} />
              <Route path="/JoinUs" element={<JoinUs />} />
              <Route path="/Tombol" element={<Tombol />} />
              <Route path="/Info" element={<Info />} />
              <Route path="/panelTips" element={<PanelTips />} />
              <Route path="/panelSoal" element={<PanelSoal />} />
              <Route path="/aturanGroup" element={<AturanGroup />} />
            </Routes>
          </div>
        </Router>
      </div>
    );
  }
}
