import React, { Component } from "react";
import { Button } from "react-bootstrap";
import { useNavigate } from "react-router-dom";

export const Panel2 = ({ conten }) => {
  const Navigate = useNavigate();
  return (
    <Button
      onClick={function () {
        Navigate("/panelSoal");
      }}
    >
      {conten}
    </Button>
  );
};
